import Vue from 'vue'
import App from './App.vue'
import shouye from './shouye.vue'
import fenlei from './fenlei.vue'
import pay from './pay.vue'
import story from './story.vue'
import about from './about.vue'
import lx from './lx.vue'
import jiameng from './jiameng.vue'
import loginin from './loginin.vue'
import loginup from './loginup.vue'
import zaixian from './zaixian.vue'
import bank from './bank.vue'
import huodao from './huodao.vue'
import VueRouter from 'vue-router'

Vue.use(VueRouter)
 
var router=new VueRouter({
	routes:[
		{
			path:'/loginin',
			component:loginin
		},
		{
			path:'/loginup',
			component:loginup
		},
		{
			path:'/',
			component:shouye
		},
		{
			path:'/kind',
			component:fenlei
		},
		{
			path:'/pay',
			component:pay
		},
		{
			path:'/story',
			component:story
		},
		{
			path:'/about',
			component:about
		},
		{
			path:'/zaixian',
			component:zaixian
		},
		{
			path:'/bank',
			component:bank
		},
		{
			path:'/huodao',
			component:huodao
		},
		{
			path:'/us',
			component:lx
		},
		{
			path:'/jiameng',
			component:jiameng
		}
	]
})
new Vue({
  el: '#app',
  router,
  render: h => h(App)
})



