<template>
	<div> 
		<img style="width: 100%;" src="https://ccdn.goodq.top/caches/d77fb68e7c449a81135cc696b98e87de/aHR0cDovLzU2OGM4MjkxOWRmYmYudDc0LnFpZmVpeWUuY29tL3FmeS1jb250ZW50L3VwbG9hZHMvMjAxNi8xMS81NDY5NWFjYzU4MmMzZjdmY2MxMDk0YWU0YTJkOTJlNS5qcGc_p_p100_p_3D.jpg" alt="" />
		<div class="pay_center">
			<div class="pay_dan" v-for="(i,$index) in pay">
				<div style="text-align: center;" class="pay_title" >
					<p style="font-size: 24px;color:#666666;">{{i.type}}</p>
					<p style="color:#666666;"><strong>{{i.etype}}</strong></p>
					<i class="glyphicon glyphicon-menu-down" style="font-size: 30px; color: #757575;"></i>
				</div>
				<div class="pay_text">
					<p style="margin-bottom: 20px;font-family: 微軟正黑體;font-size: 14px;color: #666666;">{{i.paytit}}</p>
					<div class="pay_text2" v-for="(j,$index) in i.paydata">
						<p>{{$index+1}}.{{j.name}}</p>
						<p>{{j.texts}}</p>
					</div>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
	export default {
		data: function() {
			return {
				pay: [
					{
						type: '货到付款',
						etype: 'Pay On Delivery',
						paytit: '货到付款常见问题',
						paydata: [{
							name: ' 使用货到付款，一般需要多长时间才能收到？',
							texts: '在您选择货到付款方式购买宝贝后，卖家接到订单会在线下单通知物流取货，物流公司揽件成功录单后，交易状态会从“等待卖家发货”变更为“卖家已发货”，运送时间会根据物流公司的不同而有所不同，一般需要2-7天，建议您关注哦，具体可以联系卖家咨询。'
						}]
					}
				]
			}
		}
	}
</script>

